}

.loader:before {
  content: "";
  width: 30px;
  height: 30px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation .6s linear infinite;
  position: absolute;
  bottom: 8px;
  left: 34px;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

